<template>
  <div class="beta-page">
    <div id="p-bank-viewport">
      <div id="filters">
        <ul>
            
            <li
                v-for="(item, key) in filters"
                :key="key"
            >
              {{item}}
            </li>

        </ul>
      </div>

      <div id="p-bank-content">
        <div id="filter-content">
            <router-link 
              v-for="(p, key) in pList" 
              :key="key" 
              class="opt"

              :to="{ 
                name: 'delta', 
                params: { 
                  id: p.title
                } 
              }"

            >          
              <div>
                <div class="anim-clip">
                  <div class="floater">
                    <h3>{{p.description.min}}</h3>
                    <h1>{{p.title}}</h1>
                  </div>

                </div>
              </div>
            </router-link>
        </div>
      </div>
    </div>
  </div>
