<script>
import data from "../assets/data/list.json";

export default {
  name: "Beta",
  data: function() {
    return {
      pList: data.p,
      filters: ["alpha", "beta", "charlie", "delta"],
      selectedFilters: [],
      defaultFilterState: false
    };
  },
  created: function() {
  },
  computed: {
    filteredList: function() {

        return (
            this.pList.filter((pObj) => {
                if (this.selectedFilters.length < 1 ){
                    return pObj;
                } else {
                    var currCategories = this.selectedFilters;
                    console.log("lists: ", currCategories);

                    pObj.categories.filter((objCategory) => {
                        //console.log("categories: ", objCategory);

                        for(let i=0; i <= currCategories.length - 1; i++){
                            console.log('curr category: ', currCategories[i])

                            if (objCategory == currCategories[i]){
                                return pObj
                            }
                        }
                    })

                }

            })
        )

    }
  },
  methods: {
  }
};
</script>

