<template>
    <nav id="menu-bar" v-bind:class="{ 
        isActive: active,
        animSelected: hasSelected
    }">
        <div class="nav-item about" data-navTo='/about' @click.stop='navTo'></div>
        <div class="nav-item beta" data-navTo='/beta' @click.stop='navTo'></div>
        <div class="nav-item charlie" data-navTo='/charlie' @click.stop='navTo'></div>
        <div class="nav-item delta" data-navTo='/delta' @click.stop='navTo'></div>
    </nav>
</template>

<script>

    export default {
        name: 'MenuBar',
        data: function(){
            return {
                hasSelected: false
            }
        },
        props: ['active'],
        methods: {
            navTo: function(e){
                let navLocation = e.target.getAttribute('data-navTo');
                this.$router.push(navLocation)

                this.resetMenu();
            
            },
            resetMenu: function(el){
                this.$emit('reset')

            },
        }
    }

</script>

