        // -moz-transition: height 0.3s ease-out, opacity 0.3s ease 0.5s;
        // -o-transition: height 0.3s ease-out, opacity 0.3s ease 0.5s;

        transition: 
            transform 0.3s ease-out, 
            opacity 0.3s ease;

        &.isActive {
            opacity: 1;
            visibility: visible;
            display: inline;
        }

        &.animSelected {
            transform: translate( 
                0,
                calc(var(--active-page-animation) * -1)
            )
        }

    }

    .nav-item {
        //padding: 10px 0;
        height: 5vh;
        font-weight: 800;
