        &.selected {
            background-color: purple;
            height: 100vh;

            &::after {
                transition: 
                    opacity 0.65s ease-out 0.45s;
                opacity: 0;
            }
        }

        &::after {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            margin: auto;
